.skills {
    padding: 20px;
    text-align: center;
    color: #fff;
}

.skills-heading {
    font-size: 1.8rem;
    margin-bottom: 20px;
}

.skills-section {
    display: flex;
    justify-content: center;
    gap: 20px;
    flex-wrap: wrap;
}

.skills-container {
    width: 300px;
    max-height: 400px;
    overflow: auto; /* Allow scrolling */
    scrollbar-width: none; /* For Firefox */
    -ms-overflow-style: none; /* For Internet Explorer and Edge */
}

.skills-container::-webkit-scrollbar {
    display: none; /* For Chrome, Safari, and Opera */
}

.skills-category {
    display: none;
    border-radius: 8px;
    padding: 15px;
    margin-bottom: 20px;
    overflow: hidden; /* Hide scrollbar */
}

.skills-category.show {
    display: block;
}

.skills-subheading {
    font-size: 1.5rem;
    margin-bottom: 10px;
    cursor: pointer;
    border-bottom: 2px solid #ccc; /* Changed from yellow to grey */
    transition: color 0.3s ease;
}

.skills-subheading:hover {
    color: #fff; /* Hover color */
}

.skill-item {
    background: linear-gradient(135deg, #0f0f1a, #2c2c2c);
    padding: 10px;
    border-radius: 5px;
    font-size: 0.875rem;
    margin: 5px 0;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    cursor: pointer;
}

.skill-item:hover {
    transform: scale(1.05);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
}

@keyframes fadeIn {
    0% {
        opacity: 0;
        transform: translateY(20px);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}

.skills-category {
    animation: fadeIn 0.5s ease-in-out;
}

@media (max-width: 768px) {
    .skills-heading {
        font-size: 1.5rem;
    }

    .skills-container {
        width: 100%;
    }

    .skill-item {
        font-size: 0.75rem;
        padding: 8px;
    }
}

@media (max-width: 480px) {
    .skills-heading {
        font-size: 1.2rem;
    }

    .skills-section {
        flex-direction: column;
    }

    .skills-container {
        width: 100%;
        max-height: none; /* Remove height restriction */
    }

    .skills-subheading {
        font-size: 1.2rem;
    }

    .skill-item {
        font-size: 0.7rem;
        padding: 6px;
    }
}
