.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 40px 100px;
    position: relative;
    flex-wrap: wrap;
}

.left-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    gap: 20px;
}

.right-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    gap: 20px;
    padding: 40px;
    margin-top: 50px;
    margin-left: auto;
    margin-right: 80px;
}

.profile-img {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    object-fit: cover;
    border: 2px solid #fff;
    animation: fadeIn 2s ease-out;
}

h1 {
    font-size: 2.5rem;
    color: #fff;
    margin: 0;
    animation: fadeIn 2s ease-out;
}

.show-button, .download-button {
    padding: 12px 24px;
    border: none;
    background: linear-gradient(135deg, #0f0f1a, #2c2c2c);
    color: #fff;
    cursor: pointer;
    border-radius: 5px;
    transition: all 0.3s ease;
    text-decoration: none;
    font-size: 1rem;
}

.show-button:hover, .download-button:hover {
    background: linear-gradient(135deg, #555, #aaa);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
    transform: scale(1.05);
}

@keyframes fadeIn {
    0% {
        opacity: 0;
        transform: translateY(-20px);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}

.description {
    max-width: 400px;
    color: #ddd;
    font-size: 0.875rem;
    line-height: 1.6;
    margin-top: 10px;
}

.divider {
    border: 0;
    height: 1px;
    background: #555;
    margin: 40px 0;
}

@media (max-width: 768px) {
    .header {
        flex-direction: column;
        align-items: center;
        padding: 20px;
    }

    .left-content {
        margin-bottom: 20px;
    }

    .right-content {
        margin: 10px 0;
        margin-right: 0;
        margin-left: 0;
    }

    .profile-img {
        width: 150px;
        height: 150px;
    }

    h1 {
        font-size: 2rem;
    }

    .show-button, .download-button {
        padding: 10px 20px;
        font-size: 0.875rem;
    }
}

@media (max-width: 480px) {
    .header {
        padding: 10px;
    }

    .left-content {
        margin-bottom: 15px;
    }

    .right-content {
        margin: 5px 0;
        margin-right: 0;
        margin-left: 0;
    }

    .profile-img {
        width: 120px;
        height: 120px;
    }

    h1 {
        font-size: 1.5rem;
    }

    .show-button, .download-button {
        padding: 8px 16px;
        font-size: 0.75rem;
    }
}
